import React,{Component} from 'react'
import {connect} from 'react-redux'



class SideBar extends Component {

    render(){
        return (

            <aside className='side_bar'>

                <h1>{this.props.sidebar.h1}</h1>

            </aside>

        )
    }

}

export default connect(
    state=>({
        sidebar:state.sidebar
    })
)(SideBar)


